.App {
  text-align: center;
}
body {
  margin: 0;
  padding: 0;
}
.button {
  width: 50px;
  height: 50px;
  font-size: 25px;
  margin: 2px;
  cursor: pointer;
  background: #607d8b;
  border: none;
  color: white;
}

.textView {
  width: 214px;
  margin: 5px;
  font-size: 25px;
  padding: 5px;
  border: none;
  color: #607d8b;
}
.main{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateX(-50%)translateY(-50%);
}
.bg{
  background: linear-gradient(to top, #e91e63, #3f51b5);
  height: 100vh;
}
